export default (sequelize, Sequelize) => {
  const DownloadFilesStudent = sequelize.define("downloadFilesStudent", {
    name_th: {
      type: Sequelize.TEXT,
    },
    name_en: {
      type: Sequelize.TEXT,
    },
    attachFile1: {
      type: Sequelize.TEXT,
    },
    index: {
      type: Sequelize.INTEGER,
    },
  });
  return DownloadFilesStudent;
};
